.type,
.amount,
.currency {
    border: 1px solid #e8e8e8;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    color: #838586;
}

.row:nth-child(even) {
    background-color: #ecf1f4;
}

.type {
    text-align: left;
    padding-left: calc((100% / 3) / 3);
}

.amount {
    text-align: right;
    padding-right: calc((100% / 3) / 2.5);
}
